<template>
  <div class="page page-user-devices">
    <div class="tip">
      在信任的设备上登录{{$root.title}}会视为安全使用，无需进行安全验证
    </div>
    <div
      v-for="(item, index) in list"
      :key="index"
      class="item border_bottom_1px"
    >
      <div class="name">{{ item.deviceModel }}</div>
      <div class="sign">设备号：{{ item.operatingSystem }}</div>
      <!-- <div class="platform">iphone</div> -->
      <div class="btn" @click="onDelete(item)">
        <van-icon name="delete-o"/>
      </div>
    </div>

    <van-dialog
      v-model="showFailDialog"
      class="warning"
      close-on-click-overlay
      show-cancel-button
      @confirm="deleteDevice"
    >
      <template #title>
        <van-icon name="fail"/>
      </template>
      你确认要删除该设备吗？
    </van-dialog>
  </div>
</template>

<script>
import {deleteDevice, myDeviceList} from "@/apis/user";

export default {
  data() {
    return {
      showFailDialog: false,
      list: [],
      tempUUID: "",
    };
  },
  created() {
    this.$root.$emit("root:set-topbar", ["left-back"]);
    this.load();
  },
  methods: {
    load() {
      myDeviceList({
        page: 1,
        pageSize: 50,
      }).then(({ok, msg, data}) => {
        if (ok) {
          this.list = data.list;
        } else {
          this.$toast.fail(msg);
        }
      });
    },
    deleteDevice() {
      deleteDevice({uuid: this.tempUUID}).then(({ok, msg, data}) => {
        if (ok) {
          this.tempUUID = "";
          this.$toast.success(msg);
          this.load()
        } else {
          this.$toast.fail(msg);
        }
      });
    },
    onDelete({uuid}) {
      this.tempUUID = uuid;
      this.showFailDialog = true;
    },
  },
};
</script>

